.mcard {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: 10px;
  text-align: center;
  position: relative;
  border: #0d090950 solid 1px;
  border-radius: 10px;
  overflow: hidden;
  align-items: center;
}

.imageContainer {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  background-color: black;
}

.image {
  width: 100%;
  height: auto;
  transition: transform 0.6s ease, top 0.6s ease;
  position: relative;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.9);
  transition: opacity 0.6s ease;
}

.socialIcons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.socialLink {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
}

.nameBar {
  width: 100%;
  background-color: black;
  color: white;
  text-align: center;
  padding: 5px 0;
  font-size: 16px;
  border-radius: 0 0 10px 10px;
}

.imageContainer:hover .image {
  transform: scale(0.5);
  top: -50%;
}

.imageContainer:hover .overlay {
  opacity: 1;
}